.color {
    width: auto;
    height: 20px;
    border-radius: 2px;
}
.swatch {

    display: block;
    width: auto;
    padding: 5px;
    background: #fff;
    border-radius: 1px;
    box-shadow: 0 0 0 1px rgba(0,0,0,.1);
    cursor: pointer;
}
.popover {
    position: absolute;
    z-index: 2;
}
.cover {
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
}